.search-form {
  height: 50px;
  background-color: rgba(186, 241, 218, .706);
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
  display: flex;
}

input {
  height: 20px;
  max-width: 300px;
  width: 100%;
  border: 1px solid #8f8a8a;
  border-radius: 4px;
  padding-left: 10px;
}

.btn-submit {
  height: 25px;
  width: 80px;
  cursor: pointer;
  border: 1px solid #8f8a8a;
  border-radius: 4px;
  transition: all .2s linear;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-bottom: 20px;
  display: flex;
}

.info {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.info-item {
  flex-direction: column;
  display: flex;
}

.show-more {
  height: 25px;
  cursor: pointer;
  border: 1px solid #8f8a8a;
  border-radius: 4px;
  margin: 0 auto 40px;
  transition: all .25s linear;
  display: block;
}

.btn-submit:hover, .show-more:hover {
  color: #f0f8ff;
  background-color: #92bfe4;
  border: none;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

